#content {
  margin: 0 auto 0 auto;
  padding: 0 1.5em;

  & > div {
    font-size: 1.3rem;
    margin: 1em auto 1.5em auto;
    max-width: 50ch;
    color: rgb(43, 43, 43);
    @media (prefers-color-scheme: dark) {
      color: rgb(255, 255, 255);
    }
  }

  & > .nim-code {
    max-width: 55ch;
  }

  & > h2 {
    font-size: 2.3rem;
    margin-top: 2em;
    margin-bottom: 0.5em;
    font-family: 'JetBrains Mono';
    text-align: center;
    font-weight: bold;
    color: rgb(63, 63, 63);
    
    @media (prefers-color-scheme: dark) {
      color: $color-dark-header;
    }
  }

  & > a {
    display: block;
    font-size: 1.1rem;
    margin: 0 auto 0 auto;
    padding: 0.6em;
    max-width: max-content;
  }
}
